<template>
  <div
    class="
      widgetContainer
      bg-white
      pullFundsSuccess
      appStatus appStatus--approved
    ">
    <div class="navigationHeader drawer-header-default">
      <div class="navigationHeader__inner">
        <p class="title">
          {{ $t('pull_fund_row_title') }}
        </p>
        <span class="el-icon-close" @click="done" />
      </div>
    </div>
    <div class="widgetContainer__body">
      <LottieAnimation animation-name="success" reference="pull-funds-success" :loop="true" />
      
      <p class="title">
        {{
          $t('pull_fund_transferred_successAmount', {
            value1: '$' + getPullFundResponseDetails.amount
          })
        }}
      </p>
      <p class="description" v-if="getPullFundResponseDetails.transferType !== 'debitCard'">
        {{ $t('pull_fund_transferred_successDesc') }}
      </p>

      <ul class="pullFundsSuccess__details">
        <li>
          <span class="label">{{ $t('pay_section_source') }}</span>
          <div>
            <p v-if="getPullFundResponseDetails.transferType === 'debitCard'">
              XXXX XXXX XXXX {{ getPullFundDetails.contact.debitCard.last4 }}
            </p>
            <p v-else>
              {{ getPullFundDetails.contact.ach.bankName }}
              {{ getPullFundResponseDetails.accountNumber | mask }}
            </p>
          </div>
        </li>
        <li>
          <span class="label">{{ $t('pay_section_destination') }}</span>
          <div>
            <p>{{ getSelectedAccount.label }}</p>
          </div>
        </li>
        <li>
          <span class="label">{{ $t('description') }}</span>
          <div>
            <p>{{ getPullFundResponseDetails.description }}</p>
          </div>
        </li>
      </ul>

      <div class="widgetContainer__footer--fixed">
        <el-button type="primary" @click="done" class="welcome__get-started el-button__brand brand">
          {{ $t('done') }}
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import LottieAnimation from '@/components/LottieAnimation.vue';

export default {
  components: {
    LottieAnimation
  },
  computed: {
    ...mapGetters('fund', ['getPullFundResponseDetails', 'getPullFundDetails']),
    ...mapGetters('account', ['getSelectedAccount'])
  },
  methods: {
    ...mapMutations('fund', [
      'setPullFundDetails',
      'setPullFundResponseDetails'
    ]),
    done() {
      this.drawerClose();
      setTimeout(() => {
        this.setPullFundResponseDetails(null);
        this.setPullFundDetails(null);
        this.goToDashboard();
      }, 0);
    }
  }
};
</script>

